import React from 'react'
import renderElement from '../renderMethods/renderElement'

const latexfmt = (...a) => String.raw(...a).replace('\\`', '`');

const examples = [
  'Text can be _italic_, __bold__, or ___both___.',
  'Or, *asterisks* will **accomplish** the ***same thing***.',
  latexfmt`f: x --> x^3^ - 3x^2^ + 9x - 8 doesn't look as nice as $$f: x \mapsto x^3 - 3x^2 + 9x - 8$$`,
  latexfmt`**Markdown:** a~n~ = a~n-1~ + 2n - 1 , **LaTeX:** $$a_n = a_{n-1} + 2n - 1$$`,
  latexfmt`There are two ways to write a fraction: $$\frac{24}{18} = {4 \over 3}$$`,
  latexfmt`$$V = \frac{4}{3}\pi r^3$$`,
  latexfmt`Use the quadratic formula to solve for $$x$$: $$$x = \frac{-b \pm \sqrt{b^2 - 4ac}}{2a}$$$`,
  latexfmt`Determine the measure of $$\angle{ABC}$$.`,
  latexfmt`$$\overline{AB}$$ is a line segment. $$\overleftrightarrow{CD}$$ is a line. $$\overrightarrow{EF}$$ is a ray.`,
  latexfmt`Prove that $$\triangle{ABC} \cong \triangle{DEF}$$.`,
  latexfmt`Given $$\vec{u} = \langle 3,5 \rangle$$ and $$\vec{v} = \langle -4, 9 \rangle$$, calculate $$\vec{u} \cdot \vec{v}$$.`,
  latexfmt`Remember that $$re^{i\theta} = r(\cos\theta + i\sin\theta)$$`,
  latexfmt`$$$\int_a^b f'(x)dx = f(b) - f(a)$$$`,
  latexfmt`$$$\sum_{n=0}^{\infty}\frac{f^{(n)}(a)}{n!}(x-a)^n$$$`,
  latexfmt`$$\lnot (p \land q) \iff (\lnot p \lor \lnot q)$$`,

  latexfmt`$$$
a_{n+1} = \left\{\begin{array}{r l}
  3a_n + 1 & \text{if $a_n$ is odd} \\
  a_n \div 2 & \text{if $a_n$ is even} \\
\end{array}\right.
$$$`,

  latexfmt`$$$
\left[\begin{array}{r r r}
   1 &   4 &  -9 \\
  16 & -25 &  66 \\
  49 &  64 & -81 \\
\end{array}\right]
$$$`,

  latexfmt`$$\forall\ \epsilon > 0, x \in \mathbb{R},\ \exists\ \delta > 0 \text{ such that } 0 < |x-c| < \delta \implies |f(x) - L| < \epsilon$$`,

]

const RenderExample = ({text}) => {
  return (
    <>
    <pre style={{
      backgroundColor: 'rgba(255,255,255,0.7)',
      border: '1px solid rgba(0,0,0,0.5)',
      padding: '0.5rem',
      overflowX: 'scroll',
    }}>{text}</pre>
    <span style={{
      fontFamily: 'serif', 
      fontSize: '1rem',
      paddingLeft: '0.5rem',
    }}>{renderElement(text)}</span>
    </>
  )
}

const Notes = (props) => {
  return (
    <div style={{fontSize: '0.85rem'}} onClick={props.onRequestClose}>
      
      <p>Double-click the header to edit it.</p>
      <p>
        Change the document's font by changing the default Serif font 
        in your browser's settings.
      </p>
      <p>Make sure the scale is set to 100% when printing.</p>
      <p>Create a PDF by selecting "Save to PDF" when printing.</p>

      <h4>Issues</h4>
      <p>Everything past the first page gets cut off currently.</p>
      <p>
        The Rational Graph question sometimes takes too long to generate.
        This is because the graphing algorithm is dumb.
      </p>

      <h4>Text formatting for custom questions</h4>
      <p>
        Italic and bold font, as well as superscripts, subscripts, and arrows,
        can be achieved using markdown.
      </p>
      <p>
        Text wrapped in $$ ... $$ will be rendered as a math formula using LaTeX.
      </p>
      <p>
        Use $$$ ... $$$ for display style math.
      </p>

      <h4>Examples:</h4>
      {examples.map((s, i) => (
        <div key={`render-example-${i}`} style={{marginBottom: '1rem'}}>
          <RenderExample text={s} />
        </div>
      ))}
    </div>
  )
}

export default Notes